<template>
  <div class="omikuji">
    <h1>今日の名言おみくじ</h1>
    <button @click="omikuji">おみくじを引く</button>
    <p v-if="fortune">{{ fortune }}</p>
  </div>
</template>

<script>
export default {
  name: "OmikujiComponent",
  data() {
    return {
      fortune: "",
      quotes: [
        "成功は99%の努力と1%の運",
        "人生とは人の上に立つことではなく、人と共に生きることである",
        "過去を後悔するよりも未来を創造しよう",
        "今日を精一杯生きれば、明日はまた違った景色が見える",
      ],
    };
  },
  methods: {
    omikuji() {
      const index = Math.floor(Math.random() * this.quotes.length);
      this.fortune = this.quotes[index];
    },
  },
};
</script>

